<template>
    <div id="login">
      <div class="wrap">
        <div class="infoWrap">
          <img src="../../static/img/bj02.png" alt="">
          <div class="detail">
            <div class="logo">
              <img src="../../static/img/logo.png" alt="">
            </div>
            <div class="loginTitle">
              <span>登录中心</span>
            </div>
            <div class="loginInput">
              <div class="input1">
                账号<span> / account</span>
              </div>
              <div class="input2">
                <input type="text" placeholder="请输入您的账号" v-model="formData.username">
              </div>
            </div>
            <div class="loginInput loginInput1">
              <div class="input1">
                密码<span> / password</span>
              </div>
              <div class="input2">
                <input type="password" placeholder="请输入您的密码" v-model="formData.password">
              </div>
            </div>
            <div class="loginBtn">
              <button @click="logining">登 录</button>
            </div>
          </div>
        </div>
        <div class="return" @click="fanhui">返 回</div>
      </div>
    </div>
</template>

<script>
    export default {
      name: "login",
      data(){
        return{
          formData:{
            username:'',
            password:''
          }
        }
      },
      methods:{
        logining(){
          const _this = this;
          var formData = this.formData;
          if(formData.username == ''){
            _this.$popWin('温馨提醒','请输入账号');
          }else if(formData.password == ''){
            _this.$popWin('温馨提醒','请输入密码');
          }else{
            console.log(formData)
            this.$api.axiosPost('/index/login/login',0,formData,function (data) {
              console.log(data);
              if(data.data.status == 200){
                localStorage.setItem('token',data.data.token);
                var path = sessionStorage.getItem('path');
                _this.$router.push(path);
              }else if(data.data.status == 0){
                _this.$popWin('温馨提醒',data.data.msg);
              }
            })
          }
        },
        fanhui(){
          window.history.go(-1)
        }
      },
      mounted(){

      }
    }
</script>

<style scoped>
@import "../../static/css/login.css";
</style>



